<template>
<v-card class=products>
     <!-- <v-layout row> 
            <v-dialog v-model="dialog"> 
                <template v-slot:activator="{ on }">
                    <v-btn v-on = "on">abc</v-btn>
                </template>
                <v-card>
                    <v-card-title>abc</v-card-title>
                    <v-card-text>abc</v-card-text>
                    <v-divider></v-divider>
                </v-card>
            </v-dialog>
        </v-layout> -->
    <v-subheader class = "display-3">Product</v-subheader>
    <v-card-title>
        <v-layout row> 
            <v-dialog v-model="dialog"> 
                <template v-slot:activator="{ on }">
                    <v-btn flat v-on = "on">
                        <span>New</span>
                        <v-icon right>add_circle_outline</v-icon>
                    </v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">Add New Product</span>
                        <v-spacer></v-spacer>
                        <v-btn flat icon @click="dialog = false">
                            <v-icon>cancel</v-icon>
                        </v-btn>
                    </v-card-title>
                    <ProductForm v-bind:passed-data="lastIndex"  />
                </v-card>
            </v-dialog>
        </v-layout>
        <v-spacer></v-spacer>
        <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
        ></v-text-field>
    </v-card-title>
     <v-data-table
    :headers="headers"
    :items = "products"
    class = "elevation-1"
  >
    <template v-slot:items="props">
      <td>{{ props.item.Product_ID }}</td>
      <td>{{ props.item.Kor_Name }}</td>
      <td>{{ props.item.Supplier_ID }}</td>
      <td>{{ props.item.Category_CODE }}</td>
      <td>{{ props.item.Unit }}</td>
      <td>{{ props.item.Unit_Weigh }}</td>
      <td>
          <UpdateBtn />
      </td>    
      <td>
          <DeleteBtn 
             v-bind:passed-data="props" 
             v-on:temp="deleteItem(props.item.Product_ID)"
          />
      </td>
    </template>
    <template v-slot:no-results>
        <v-alert :value="true" color="error" icon="warning">
            Your search for "{{ search }}" found no results.
        </v-alert>
    </template>
  </v-data-table>
</v-card>
</template>

<script>
import DeleteBtn from '@/components/DeleteBtn'
import UpdateBtn from '@/components/UpdateBtn'
import ProductForm from '@/components/ProductForm'
export default {
    components: { DeleteBtn, UpdateBtn, ProductForm } ,
    created: function () {
        // Call methods to show list
        this.getProducts()
        // Call last Index of products to use for adding new item
        this.getLastProductId()
        

    },
    data() {
        return {
            headers: [
                { text: 'ID', value: 'Product_ID' },
                { text: 'Name(Kor)', value: 'Kor_Name' },
                { text: 'Supplier', value: 'Supplier_ID' },
                { text: 'Category', value: 'Category_CODE' },
                { text: 'Unit', value: 'Unit' },
                { text: 'Unit Weight', value: 'Unit_Weigh', sortable: false  },
                { text: 'Update', sortable: false },
                { text: 'Delete', sortable: false }
            ],
            products: [ {Product_ID: "1"}, {Product_ID:"2"}],
            dialog: false,
            category: ["a", "b"],
            supplier: ["c", "d"],
            lastIndex: []
        }
    },
    methods:{
        getProducts: function(){
            this.$http.get('/api/products').then((response) => {
            this.products = response.data
            })
        },
        getLastProductId: function(){            
            this.lastIndex = this.products.length
            console.log("---------------->"+this.lastIndex)
        },
        deleteItem: function(productId){
            console.log("tests")
            console.log(productId)
            // (Dummy data) 1. loop 이용해서 일일히 비교해서 삭제하기
            // for(var i =0; i<this.products.length; i++){
            //     if(this.products[i].id===paraId){
            //         this.products.splice(i, 1)
            //     }
            // }          
            // (Dummy data) 2. findIndex 메서드 사용하여 인덱스 찾은 다음에 삭제하기
            // var index = this.products.findIndex(p=>p.id == paraId)
            // this.products.splice(index, 1)

            // (Actual data) 데이터 베이스의 해당 제품 삭제하기
            this.$http.delete(`/api/products/delete/${productId}`)
            .then((response) => {
                window.location.reload();
            }).catch(function(error) {
                alert('Failed to Delete!')
            }) 
        },
        addNewItem: function() {
            console.log("Hello add Item")
        }
    }
}
</script>
<style scoped>

</style>


