<template>
<div>
  <v-form
  ref="form"
  >
      <!-- <v-card-title>
        <span class="headline">Add New Product</span>
      </v-card-title> -->
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs 12 sm2>
              <v-text-field v-model="proId" ref="myid" name="Product_ID" label="Product ID" readonly></v-text-field>
            </v-flex>
            <v-flex xs12 sm5>
              <v-text-field name="Kor_Name" label="Korean Name" required></v-text-field>
            </v-flex>
            <v-flex xs12 sm5>
              <v-text-field name="Eng_Name" label="English Name"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-select
              :items = "category"
              item-text = "code_name"
              item-value = "code"
              label="Category"
              ></v-select>
            </v-flex>
            <v-flex xs12 sm6>
              <v-select
              v-model = "supplier"
              :items = "supplier"
              label = "Supplier"
              name="Supplier_ID"
              ></v-select>
            </v-flex>
            <v-flex xs12 sm6 md4>
              <v-text-field name="Unit" label = "Unit"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md4>
              <v-text-field name="Unit_Weigh" label = "Unit Weight"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md4>
              <v-text-field name="Price" label = "Price"></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions class="justify-center">
        <v-btn @click="clear">Clear</v-btn>
        <v-btn v-on:click="sendForm">Save</v-btn>
      </v-card-actions>
  </v-form>
</div>
</template>
<script>
export default {
  created () {
    this.getCategory()
    this.setFormAddOrUpdate()
  },
  props: [ 'passedData' ],
  data() {
    return {
      category: [],
      supplier: ["temp1"],
      dialog: false,
      selected: this.passedData, // selected true=> add, selected false => update
      proId:[]
    }
  },
  methods: {
    sendToParent: function(id){
      this.$emit('sendToParent', id)
      // this.$emit('sendToParent') v-on:click="sendToParent(passedData.item.id)"
    },   
    temp: function(){
      this.dialog =false
      this.$emit('temp')

    }, getCategory: function(){
      // get Category list from DB
      this.$http.get('/api/categories').then((response) => {
        this.category = response.data
      })
      console.log("Get Category Console")
    }, setFormAddOrUpdate: function() {
      this.proId = this.selected+1;
      // set the form if add or update
      // selected true=> add, selected false => update
    }, sendForm: function() {
      if(parseInt(this.selected, 10)){
         console.log("this form for Add")
      }
      // if ( this.selected=true ){
      //   console.log("this form for Add")
      //   this.$http.post('/api/products/add', {

      //   }).then((response) => {
      //     alert('add success')
      //   }).catch(function(error){
      //     alert('add failed')
      //   })
      // } else if (this.selected=false) {
      //   console.log("this form for update")
      // } else {
      //   console.log("Error to set form")
      // }
    }, clear: function() {
      console.log("clear button")
      this.$refs.form.reset()
      // reset 하기 id 빼고
      // this.$$refs.myid='hey';
      // $("Product_ID").val("Hey")
      // document.getElementsByName('Product_ID').value='hey';
      
    }
  }
}
</script>




