<template>
 <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template>
        <v-btn
          flat
          icon
          slot = "activator"
        >
        <v-icon>delete</v-icon>
        </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
          Delete Confirmation
        </v-card-title>

        <v-card-text>
          You won't be able to revert back.
          Are you sure to delete?
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="warning"
            flat
            v-on:click="temp"
          >
            Confirm
          </v-btn>
          <v-btn
            color="primary"
            flat
            @click="dialog = false"
          >
            Back
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  props:['passedData'],
  data() {
    return {
      dialog: false,
      selected: this.passedData
    }
  },
  methods: {
    sendToParent: function(id){
      this.$emit('sendToParent', id)
      // this.$emit('sendToParent') v-on:click="sendToParent(passedData.item.id)"
    },   
    temp: function(){
      this.dialog =false
      this.$emit('temp')

    }
  }
}
</script>




